<template>
  <div>
    <!-- Dataset Upload Section -->
    <div class="section">
      <h2>Upload Dataset</h2>
      <input type="file" accept=".zip" ref="fileInput" @change="handleFileUpload" />
      <button class="upload-data" @click="uploadDataset">Upload</button>
      <p v-if="uploadMessage">{{ uploadMessage }}</p>
    </div>

    <!-- Model Configuration Section -->
    <div class="section">
        <h2>Model Configuration</h2>
        <label>Model Architecture</label>
        <select v-model="selectedModel">
          <option disabled value="">Please select a model</option>
          <option v-for="model in modelList" :key="model" :value="model">{{ model }}</option>
        </select>

        <label for="transfer-learning-option" class="model-name" v-if="selectedModel === 'Add Custom Model'">Use ResNET50 As Base Model</label>
        <input class="model-name-input" type="text" v-model="customModelName" v-if="selectedModel === 'Add Custom Model'" placeholder="e.g. Custom 5 CNN">

        <label for="transferLearningOption" class="model-name" v-if="selectedModel === 'Add Custom Model'">Use ResNET50 As Base Model</label>
        <select class="transfer-learning" v-model="transferLearningOption" v-if="selectedModel === 'Add Custom Model'">
          <option disabled value="">Perform Transfer Learning</option>
          <option>No</option>
          <option>Yes</option>
        </select>
    </div>

    <div class="section">
      <button class="save-model-button" v-if="selectedModel === 'Add Custom Model'" @click="saveModel">Save Model</button>
        <p v-if="modelSavedMessage">{{ modelSavedMessage }}</p>

        <!-- Display textarea when 'Custom' is selected -->
        <textarea class="custom-model" v-if="selectedModel === 'Add Custom Model'" v-model="customModelJSON" placeholder="..."></textarea>   
    </div>

    <!-- Training Configuration Section -->
    <div class="section">
      <h2>Training Configuration</h2>
      <label>Framework</label>
      <select class="selectFramework" v-model="framework">
          <option disabled value="">Please select a framework</option>
          <option v-for="framework in frameworks" :key="framework">{{ framework }}</option>
        </select>
        
      <label>Learning Rate</label>
      <input class="train-configs" type="text" v-model="learningRate" placeholder="Learning Rate" />
      <label>Epochs</label>
      <input class="train-configs" type="text" v-model="epochs" placeholder="Epochs" />
    </div>

    <div class="trainconfig2">
      <label>Optimizer</label>
      <select v-model="optimizer">  
        <option disabled value="">Please select an optimizer</option>
        <option>Adam</option>
        <option>RMSprop</option>
        <option>SGD</option>
        <option>Adagrad</option>
      </select>      

      <label class="trainconfig2-labels">Batch Size</label>
      <input class="train-configs" type="text" v-model="batchSize" placeholder="Batch Size" />
    </div>

    <div>
        <button class="train-button" @click="startTraining">Train Model</button>
    </div>

    <!-- Training Progress Bar -->
    <div class="section">
      <vue-progress-bar></vue-progress-bar> <!-- from 'vue-progressbar' package -->
    </div>

    <!-- Placeholder for TensorBoard -->
    <div class="section">
      <h2>Training Metrics</h2>
      <!-- This is a placeholder. Actual TensorBoard embedding will require backend setup. -->
      <div id="tensorboard-placeholder">TensorBoard will be embedded here.</div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { VueProgressBar } from 'vue-progressbar'

export default {
  name: 'ModelTraining',
  components: {
    VueProgressBar
  },
  computed: {
    modelList() {
      return this.$store.state.modelList;
    }
  },
  data() {
    return {
      selectedModel: '',
      customModelJSON: '',
      learningRate: 0.001,
      epochs: 10,
      batchSize: 32,
      optimizer: 'Adam',
      framework: '',
      frameworks: ['PyTorch', 'TensorFlow'],
      modelName: '',
      transferLearningOption: '',
      modelSavedMessage: '',
      customModelName: '',
      uploadMessage: '',
      customModel: '',
      file: null
    };
  },
  methods: {
    handleFileUpload() {
      const fileInput = this.$refs.fileInput;
      if (fileInput.files.length > 0) {
        this.file = fileInput.files[0];
        this.uploadMessage = 'File is ready to be uploaded';
      } else {
        this.uploadMessage = 'Please select a file';
      }
    },
    uploadDataset() {
      // This method will be used to upload a dataset but for now it is has placeholder functionality with bare minimum logic for error handling
      if (!this.file) {
        this.uploadMessage = 'Please select a file';
        return;
      }
      // Here you would normally create a FormData object and append the file to it
      // and then send a request to your backend server to handle the file upload.
      // For this example, we'll just simulate an upload.
      this.uploadMessage = 'Uploading...';
      setTimeout(() => {
        this.uploadMessage = 'File uploaded successfully';
      }, 2000);
    },
    saveModel() {
      if (this.selectedModel === 'Add Custom Model' && this.customModelJSON) {
        // Save the custom model configuration
        this.customModel = JSON.parse(this.customModelJSON);
        this.modelSavedMessage = 'Custom model configuration saved successfully';
      } else {
        this.modelSavedMessage = 'Please enter the custom model configuration';
      }
    },
    startTraining() {
      // Validate input data
      if (!this.selectedModel) {
        alert('Please select a model');
        return;
      }

      // Construct the payload with the training parameters
      const trainingParams = {
        model: this.selectedModel,
        learningRate: this.learningRate,
        epochs: this.epochs,
        batchSize: this.batchSize,
        optimizer: this.optimizer,
        // Include other parameters as needed
      };

      // Send the training parameters to the Flask API
      axios.post('https://your-flask-api-endpoint/start-training', trainingParams)
        .then(response => {
          // Handle the response from the Flask API
          console.log('Training started:', response.data);
          // Potentially update the UI to show progress or training status
        })
        .catch(error => {
          // Handle any errors
          console.error('Error starting training:', error);
          alert('Failed to start training. Please try again.');
        });
    }
  }
};
</script>

<style scoped>
.section {
  margin-bottom: 100px;
  margin-left: 30px;
}

.trainconfig1 {
  margin-bottom: 100px;
  margin-left: 30px;
}

.trainconfig2 {
  margin-bottom: 100px;
  margin-left: 30px;
}

label {
  font-weight: bold;
  margin-right: 20px;
}

.trainconfig2-labels {
  font-weight: bold;
  margin-right: 20px;
  margin-left: 50px;
}

.model-name {
    font-weight: bold;
    margin-left: 100px;
}

.transfer-learning {
  font-weight: bold;
  width:10%;
}


input {
    margin-right: 30px;
    color: var(--global-primary-text-color);
    font-weight: bold; 
}


.model-name-input {
    margin-right: 30px;
    color: var(--global-primary-text-color);
    border: 4px solid var(--global-primary-text-color);
    background-color: #ffffff;
    font-weight: bold; 
}

.train-configs {
    margin-right: 30px;
    color: var(--global-primary-text-color);
    border: 4px solid var(--global-primary-text-color);
    background-color: #ffffff;
    font-weight: bold; 
}

button.train-button {
    font-weight: bold;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    display:block;
    color: var(--global-secondary-text-color);
    border: 4px solid var(--global-primary-text-color);
    padding: 8px 8px;
}

button.upload-data {
    margin:0 auto;
    margin-left: 300px;
    color: var(--global-secondary-text-color);
    border: 4px solid var(--global-primary-text-color);
    padding: 8px 20px;
}

button.save-model-button {
    margin:0 auto;
    margin-left: 640px;
    color: var(--global-secondary-text-color);
    border: 4px solid var(--global-primary-text-color);
    padding: 8px 20px;
}

h2 {
    margin-bottom: 30px;
}

.custom-model {
  margin-top: 30px;
  width: 83%;
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  font-size: 16px;
  font-weight: bold;
  resize: none;
  border-color: var(--global-primary-text-color)
}

select {
  font-weight: bold;
  color: var(--global-primary-text-color);
  border: 2px solid var(--global-secondary-text-color);
}

.selectFramework {
  margin-right:30px;
}

</style>
